.Header a{color:rgba(0,0,0,.788)}.site-header{background:#ffbe33;width:100%;height:80px;padding:10px 0;z-index:5}.site-header__wrapper{display:flex;justify-content:space-between;width:100%;height:100%}.brand{width:100%;font-weight:bold;font-size:1rem;text-decoration:none;font-family:"Noto Sans JP","ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic Pro","メイリオ","Meiryo","游ゴシック","Yu Gothic","ＭＳ Ｐゴシック","MS PGothic",sans-serif;letter-spacing:1px}.brand h1{margin:10px 0 0 12rem;color:#000}.nav{text-align:right;width:100%;height:100%;margin-right:10rem}.nav__toggle{display:none}.nav__wrapper{margin:15px auto}.nav__wrapper ul{display:flex}.nav__item{display:inline-block}.nav__item a{font-size:20px;margin:0 40px;padding:0;text-decoration:none;cursor:pointer}.signOut{background-color:rgba(0,0,0,0);border:none;cursor:pointer;outline:none;padding:0;appearance:none;font-size:18px}.cp_cont{display:none;left:-100%}@media screen and (min-width: 321px)and (max-width: 1300px){.brand h1{margin-left:10rem}.nav{margin-right:0}}@media screen and (min-width: 321px)and (max-width: 1100px){.brand{font-size:.9rem}.brand h1{margin-left:4rem}.nav{margin-right:2rem}.nav__item a{margin:0 20px}}@media screen and (min-width: 321px)and (max-width: 880px){.nav__wrapper{display:none}.nav__toggle{display:inline-block}.signOut{display:block;padding:20px;width:100%;text-align:left;text-decoration:none;color:#fff;border-bottom:1px solid #fff}}@media screen and (min-width: 321px)and (max-width: 450px){.site-header{height:60px}.brand{font-size:10px;padding-left:20px;margin:0}.brand h1{margin:7px}}